export default {
	data() {
		return {
			inputValue: '',
			carts: [],
			obj: ['一年顶十年',
				'儿童大脑 开窍指南',
				'大话西方艺术',
				'如何高效学习:1年完成MIT4年33门课程的整体性学习法',
				'性格的力量'
			]
		}
	},
	methods: {
		touch(item){
			this.inputValue=item
			this.getdate()
		},
		search() {
			if (this.inputValue.trim().length == 0) {
				uni.showModal({
					title: '搜索内容不能为空',

				});
			} else {
				if (!this.carts.includes(this.inputValue)) {
					this.carts.unshift(this.inputValue);
					uni.setStorage({
						key: 'searchList',
						data: this.carts
					});
				} else {
					//有搜索记录，删除之前的旧记录，将新搜索值重新push到数组首位
					let i = this.carts.indexOf(this.inputValue);
					this.carts.splice(i, 1);
					this.carts.unshift(this.inputValue);
					// uni.showToast({
					// 	title: '请勿重复输入'
					// });
					uni.setStorage({
						key: 'searchList',
						data: this.carts
					});
				}
			}
		},
		getdate() {
			this.search()
		},
		empty() {
			uni.showToast({
				title: '已清空'
			});
			uni.removeStorage({
				key: 'searchList'
			});

			this.carts = [];
		}
	},
	async onLoad() {
		let list = await uni.getStorage({
			key: 'searchList'
		});
		console.log(list);

		if (list[1].data) {
			this.carts = list[1].data;
		}
	}


}
